import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { SuportePage } from '../../suporte/suporte';
@Component({
  selector: 'page-comercial',
  templateUrl: 'comercial.html'
})
export class ComercialPage {

   diseases = [
     
    { title: "O que  Marketing Multinvel (MMN) Sweet Hair?", description: " um modelo de vendas diretas, via mononvel/binvel, de produtos de alta qualidadede, tecnolgia e perfomance no mercado de csmeticos de uso profissional e para consumidor final." },
     
    { title: "Como fao para revender os produtos da Sweet Hair?", description: "Primeiramente voc precisar de um lder, os lderes so pessoas conhecedoras do MMN que esto incorporando esse grande conhecimento a Sweet Hair. O lder ir te orientar sobre todas as diretizes do Plano de Ngocio e te ajudar a desenvolver um bom trabalho. Caso voc no conhea ningum entre em contato conosco atravs do e-mail contato@sweethair.com.br (com as seguintes informaes: nome completo, telefone de contato de preferncia que tenha (WhatsApp), sua regio cidade e estado) e seu interesse em fazer parte do MMN Sweet Hair) que lhe conectaremos ao lder mais proximo de sua regio." },
     
    { title: "Como me ativo no sistema Marketing Multinvel (MMN) Sweet Hair?", description: "Aps se cadastrar no sistema e adquirir seu login e senha de acesso, dever obter um de nossos combos de R$800,00, R$2.100,00 ou R$4.100,00. Desta forma voc como distribuidor independente, poder realizar todas as funcionalidades, usufruir do sistema com ferramenta de trabalho e dos benefcios nele incorporados." },
     
    { title: "O que  upgrade e como fao?", description: "O upgrade  a troca do seu plano de combo atual para um maior. Fazer o upgrade  muito simples, entre no sistema com seu login e senha e solicite o upgrade, efetue o pagamento da diferena do combo. Ex: Voc adquiriu o combo de R$800,00 na sua ativao e deseja fazer o upgrade, ento voc o solicita no seu cadastro e escolhe entre as opes de R$2.100,00 ou de R$4.100,00, seguindo isso  s pagar a diferena, nesse caso supondo que escolha o de R$2100,00 voc ir pagar a diferena de R$1.300,00 e receber em seu estabelecimento os produtos que complementaro o primeiro combo adquirido de R$800,00." },
     
    { title: "Como so feitas as vendas para o salo e quais so seus ganhos?", description: "As vendas so feitas atraves do sistema Ezitus Sweet MMN onde o salo  cadastrado. Ao adquirir um dos combos de produtos oferecidos, o salo tem como beneficios: 60% de desconto nas suas recompras, lembrando sempre que para que este salo tenha direito ao beneficio, dever estar ativo no sistema (para isso ele dever dispor de um valor de R$150,00 para a manueno do sistema e um consumo mnimo em produtos de R$350,00 para o beneficio do frete gratuito." },
     
    { title: "Como so feitas as vendas para o consumidor final?", description: "As vendas para consumidor final que no tem cadastro no sistema sero realizadas atravs da vitrine disponvel no cadastro do distribuidor independente. Aps a recompra feita, os produtos sero repassados ao consumidor final." }
    
  ];

  shownGroup = null;

  constructor(public navCtrl: NavController) {
  }

  back(){
       this.navCtrl.setRoot(SuportePage);
  }

  toggleGroup(group) {
      if (this.isGroupShown(group)) {
          this.shownGroup = null;
      } else {
          this.shownGroup = group;
      }
  }
  isGroupShown(group) {
      return this.shownGroup === group;
  }
}


