import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { SuportePage } from '../../suporte/suporte';

@Component({
  selector: 'page-tecnica',
  templateUrl: 'tecnica.html'
})
export class TecnicaPage {

 diseases = [
     
    { title: "Qual o campo de atuao de um tcnico Expert Sweet?", description: "A rea Tcnica tem por objetivo fornecer todo suporte necessrio para o xito das vendas e garantia de uma performance 100% eficaz de todos os produtos Sweet, tanto profissionais quanto para o cliente final. Dessa forma, nossos Experts esto preparados para:  1. realizar cursos e wokshops voltados  rea comercial (lderes e distribuidores independentes) no que se refere ao treinamento dos produtos e estratgias de vendas dos mesmos;  2. realizar cursos e workshops direcionados aos profissionais cabeleireiros desenvolvendo tcnicas associadas  utilizao dos produtos; 3. realizar visitas e apresentaes em sales; 4. realizar suporte via e-mail, WhatsApp e telefone para esclarecer dvidas e solucionar problemas; 5. fornecer material apostilado explicativo e tcnico.Todos os cursos podero ser realizados em nossas sedes em So Paulo, bem como nas cidades de atuao do lder ou distribuidor independente." },
     
    { title: "Como solicitar um tcnico?", description: "Para fazer a solicitao de um tcnico, seja para o desenvolvimento de um evento na sua cidade, em nossas sedes em So Paulo ou no Instituto Sweet, bem como para solicitar visita em sales o procedimento dever ser realizado via email. Voc dever enviar uma solicitao para o email: mmn@sweethair.com.br descrevendo qual tipo de evento e qual data. O setor responsvel responder de maneira rpida e objetiva informando com relao  existncia de disponibilidade e encaminhando demais protocolos para preenchimento. Devemos lembrar que a agenda ser liberada de acordo com a disponibilidade dos tcnicos Experts e caso seja de interesse do lder ou do distribuidor independente, a Sweet tambm forma tcnicos atravs do Curso Expert." },
     
    { title: "Como se tornar um tcnico Expert Sweet", description: "A Sweet Hair possui um curso voltado para o profissional cabeleireiro que deseja se tornar um Tcnico Expert. Esse curso acontece normalmente uma vez por ms e as vagas so limitadas. Trata-se de um curso com no mximo 15 profissionais que aprenderam ao longo de 1 semana todos os conceitos da marca, composio qumica dos produtos, atuao na estrutura capilar, tcnicas de aplicao, desenvolvimento de capacidade de resolver problemas e dar suporte para os profissionais cabeleireiros, de forma terica e prtica. As inscries devero ser feitas e confirmadas atravs do email: adriana.picolo@sweethair.com.br. Aviso importante: A SWEET HAIR NO SE RESPONSABILIZA PELA ATUAO TCNICA DE PROFISSIONAIS CABELEIREIROS QUE NO TENHAM REALIZADO NOSSO CURSO DE FORMAO EXPERT." },
     
    { title: "O que  o The First?", description: "O The First  um sistema de alisamento trmico desenvolvido sob a forma de shampoo com o objetivo de facilitar a aplicao e otimizar o tempo do profissional cabeleireiro. O processo de aplicao de uma progressiva normal leva em mdia 4 horas. Realizando o processo de alisamento com o The First, o profissional consegue realizar o mesmo procedimento em 2 horas. Ou seja, h muito mais agilidade e rapidez, possibilitando que o profissional realize mais trabalhos durante o dia."},
     
    { title: "O The First ou a escova Lovely desbotam ou alteram a cor dos cabelos?", description: "No, nenhum dos dois produtos alteram a cor dos cabelos, porm, temos que considerar que qualquer alisamento trmico precisa da fonte de calor que vem da prancha e essa fonte de calor pode ocasionar oxidao da cor do cabelo. Essa oxidao pode ser notada se o cabelo for extremamente poroso, se o tipo de colorao utilizada for superficial, como tonalizantes ou mscaras ou se a cliente possuir muitos cabelos brancos, o que gera a revelao do fundo de clareamento. Por esse motivo indicamos sempre realizar o procedimento de colorao ou descolorao aps a realizao do alisamento, seja ele efetuado com The First ou Lovely." },
     
    { title: "Podemos usar The First em cabelos coloridos ou com descolorao?", description: "Sim. O The First pode ser usado em todos os tipos de cabelo. O que devemos sempre levar em conta  a sade dos fios para determinar qual temperatura ser utilizada. Em cabelos sensibilizados ou danificados a temperatura da prancha no deve ultrapassar 200C (ou 410F). Para cabelos saudveis, a temperatura utilizada na prancha deve ser de 230C (ou 450F)." },
     
    { title: "O shampoo The First  compatvel com todas as qumicas?", description: "Sim. Tanto o The First quanto a Lovely so produtos  base de cidos que agem nas pontes salinas do crtex do cabelo, no tendo nenhum tipo de incompatibilidade com nenhuma qumica. Sendo assim, cabelos com colorao, descolorao, relaxamentos alcalinos a base de hidrxidos ou tioglicolato, hen, etc. podem ser processados com os produtos Sweet. Lembrando que se o cabelo estiver sensibilizado, a temperatura usada na prancha no dever ser superior  200C." },
     
    { title: "Qual o pH do shampoo The First?", description: "O shampoo The First possui pH de 2,0, sendo que, na aplicao no cabelo, com a presena de gua, esse pH mantem-se um pouco mais elevado." },
     
    { title: "O The First amarela cabelos loiros?", description: "No. Se o processo for feito da forma correta, no ocasiona amarelamento. O mximo que pode acontecer , num cabelo matizado com mscara ou tonalizado, pode ocorrer perda do pigmento acinzentado ou irisado e revelar o fundo de clareamento. Ou seja, se o cabelo loiro estiver na altura de tom 10 acinzentado, aps o procedimento ele vai estar na altura do tom 10, porm, sem o acinzentado." },
     
    { title: "Qual a melhor forma de fazer o alisamento em um cabelo com luzes ou colorao?", description: "Em cabelos que j tenham luzes, o The First pode ser feito normalmente, respeitando a sade dos fios. Caso a cliente queira fazer os dois procedimentos: alisar e descolorir, sugerimos que primeiro o profissional faa o alisamento com o The First ou com a Lovely. Assim que finalizar o processo de prancha, o cabelo dever ser lavado (com um shampoo normal) e condicionado para haver normalizao do pH do cabelo, seco e em seguida pode-se proceder a descolorao. O mesmo processo deve ser feito para colorao. Caso no seja feito no mesmo dia, deve haver obrigatoriamente uma lavagem completa do cabelo entre um processo e outro." },
     
    { title: "Qual a diferena entre o The First e a Lovely?", description: "Os dois produtos alisam do mesmo jeito, entretanto, a Lovely  um produto mais tradicional, com 3 passos que automaticamente requer um pouco mais de tempo na aplicao." },
     
     { title: "O The First e a Lovely possuem formol na sua composio?", description: "No. Os dois produtos so compostos por cidos que tambm so utilizados pela rea de esttica facial. Nenhum dos dois possui formol na sua composio." },
     
     { title: "O The First ou a Lovely podem causar reaes alrgicas?", description: "No. Temos laudos dermatolgicos que comprovam que a utilizao correta de ambos no ocasionam reaes alrgicas, porm, se a cliente possuir feridas ou doenas no couro cabeludo, indicamos que no sejam realizados os procedimentos pois tratam-se de produtos cidos que podem acentuar os sintomas ou gerar incmodos." },
     
     { title: "Qual a porcentagem de alisamento que conseguimos obter com o The First?", description: "Tanto o The First quanto a Lovely alisam 100% os cabelos ondulados, cacheados e crespos. Cabelos afro carapinhas ou crespos muito resistentes, utilizando um dos dois produtos obtemos de 70 a 90% de alisamento, entretanto, temos um protocolo de aplicao denominado semidefinitiva para cabelos afros que combina a utilizao do kit Lovely com o The First para obtermos 100% de alisamento em uma nica aplicao para esse tipo de cabelo, sem a necessidade de utilizar relaxamentos alcalinos." },
     
     { title: "Mulheres grvidas, lactantes e crianas podem utilizar os alisamentos Sweet (The First ou Lovely)?", description: "Temos laudos e estudos que comprovam que os componentes dos alisamentos Sweet no caem na corrente sangunea. Da mesma forma, durante a aplicao no ocorre a produo de fumaa densa e abundante, no havendo risco de intoxicao. Entretanto, sugerimos que gestantes e lactantes peam autorizao mdica antes do procedimento, uma vez que podem haver restries ou outros problemas de sade pr-existentes que o profissional cabeleireiro no ter condies de identificar. Para crianas, sugerimos aps os 12 anos de idade, uma vez que antes dessa idade, dificilmente a criana conseguir manter-se parada para a realizao do procedimento." },
     
     { title: "Cabelos brancos podem sofrer alteraes de cor durante os procedimentos de alisamento?", description: "Sim. Os cabelos brancos no possuem melanina, ou seja, nenhum tipo de pigmento. Durante o processo de alisamento pode ocorrer oxidao do cabelo e revelar um tom amarelado. Isso pode ser evitado com a utilizao de uma temperatura mais baixa na prancha ou utilizao de matizadores associados aos alisamentos." },
     
     { title: "Qual a durabilidade dos alisamentos Sweet?", description: "Tanto para The First quanto para a Lovely, a durabilidade mdia  de 2 a 4 meses, podendo ser estendido com o uso de produtos adequados de manuteno e utilizao de fonte de calor (secador com ar quente)." },
     
     { title: "O alisamento sai do cabelo com o tempo?", description: "Sim. Caso a cliente resolva no alisar mais os cabelos, a mesma dever aguardar um perodo de 9 meses a 1 ano para que o cabelo volte completamente  forma original (considerando que o cabelo no possua outros produtos de alisamento)." },
     
     { title: "Qual a melhor forma de realizar um procedimento de alisamento com The First?", description: "A melhor forma de realizar um procedimento de alisamento com o The First  sempre de acordo com o diagnstico dos cabelos. Sendo assim, devemos sempre avaliar as reais necessidade do cabelo e realizar o protocolo que mais se enquadra s mesmas. Para isso, consulte as diferentes formas de aplicao do The First. 01  Para cabelos loiros  retirar frizz Misture 35 ml do shampoo The First com 15 ml do Shampoo Matizador. Deixe agir de 10 a 20 minutos, enxgue 100%, seque os cabelos e finalize com a prancha. 02  Reconstruo e nutrio Aps os 20 minutos de pausa com The First, enxgue 100% e aplique a Mscara de Tratamento Cronology. Deixe agir por 7 minutos, enxgue 100%, seque e finalize com a prancha. 03  Reparao de pontas danificadas Aps os 20 minutos de pausa com The First, enxgue 100% e aplique nas pontas danificadas o S.O.S 1 Passo, Seque 100% e finalize com a prancha. 04  Semi definitiva Aplique 50ml de The First por todo o cabelo, deixe agir por 20 minutos e enxgue 100%. Seque os fios 80% e aplique sob os cabelos o Hair Treatment 2 Passo, da linha Lovely. Deixe agir por 15 minutos, enxgue 100%, seque os cabelos e pranche 7 vezes cada mecha. 05  Espelhamento Capilar Aps os 20 minutos de pausa com The First, enxgue 100% e aplique a Mscara Touch of Silk 3 Passo, da linha Lovely. Deixe agir por 10 minutos, enxgue 100%, seque os cabelos e pranche 7 vezes cada mecha. 06  Colorao Aps realizar o procedimento completo do The First, ou seja, aps pranchar, molhe abundantemente o cabelo, seque com o auxlio do secador e aplique a colorao conforme orientao do fabricante. Aps o tempo de pausa da colorao, enxague novamente. Para melhor sensorial e durabilidade da cor, sugerimos finalizar com o Touch of Silk ou com a Mscara Hair Cream da linha Cronology. 07  Descolorao Aps realizar o procedimento completo do The First, ou seja, aps pranchar, lave os cabelos com Shampoo e Condicionador de sua preferncia (sugerimos a Linha Mousse), seque com o auxlio do secador e realize o procedimento de descolorao. Em seguida realize a matizao ou tonalizao, conforme desejar e, para melhor sensorial e durabilidade da cor, sugerimos finalizar com o Touch of Silk ou com a Mscara Hair Cream da linha Cronology. 08  The First para loiras  Alisamento Aps os 20 minutos de pausa com The First, enxgue 100% e aplique a Mscara Platinum e deixe agir por 10 a 15 minutos at obter o efeito chumbado, enxgue 100%, seque os cabelos e pranche de 5 a 7 vezes cada mecha com a temperatura de acordo com a sade do cabelo." },
     
     { title: "Por que devemos usar a manuteno ps progressiva do The First?", description: "O uso da famlia The First garante maior durabilidade do alisamento exatamente por possuir na sua composio uma matria prima chamada N-duhance, a qual age no crtex do cabelo, promovendo alinhamento, efeito anti-frizz e anti umidade e mantendo as cutculas seladas." },
     
     { title: "Qual  o pigmento base presente na colorao Royal Colour da Sweet (Qual o fundo da colorao)?", description: "A colorao Royal Colour no possui um nico tom no fundo das suas bases, mas para cada altura de tom temos um fundo que neutraliza o fundo de clareamento indesejado, a saber: 1.0 ao 4.0  fundo mate (responsvel por neutralizar o fundo de clareamento vermelho) 5.0  fundo mate azulado (responsvel por neutralizar o fundo de clareamento vermelho alaranjado) 6.0  fundo azul (responsvel por neutralizar o fundo de clareamento laranja) 7.0  fundo azul violeta (responsvel por neutralizar o fundo de clareamento amarelo alaranjado) 8.0 ao 10.0  fundo violeta (responsvel por neutralizar o fundo de clareamento amarelo)." },
     
     { title: "Posso tonalizar um cabelo usando a colorao Royal Colour?", description: "Sim, porm, trata-se de colorao permanente. Para obter um efeito de tonalizao com a linha Royal devemos utilizar oxidante de 10 volumes, na proporo de 1 parte de colorao para 2 partes de OX e o tempo de pausa no pode exceder 10 minutos no cabelo para no haver alterao da cor do cabelo natural da cliente." },
     
     { title: "Consigo cobrir brancos com a colorao da Sweet?", description: "Sim. A colorao Royal Colour cobre 100% cabelos brancos desde que seja utilizada a regra abaixo: at 50% de brancos espalhados por toda a cabea, podemos utilizar apenas as nuances escolhidas, de 50% a 70% de brancos devemos usar metade da nuance e metade da base, de 70% a 100% de cabelos brancos devemos usar  de base e  de nuance escolhida. O oxidante escolhido deve ser de acordo com quantos tons queremos oxidar do cabelo escuro e o tempo de pausa para cabelos brancos deve ser de 45 a 50 minutos." },
     
     { title: "Quantos tons consigo clarear com o p descolorante da Sweet?", description: "O p descolorante Sweet clareia de 7 a 8 tons, lembrando que ele possui partculas anti-amarelo que auxiliam na obteno do clareamento desejado." },
     
     { title: "Por que o p descolorante da Sweet  branco?", description: "Desenvolvemos um p descolorante branco porque os ps azuis ou violetas que existem no mercado possuem somente corante azul e/ou violeta, matria prima que no tem poder de neutralizar o fundo de clareamento do cabelo, mas simplesmente mascarar a real cor obtida com a descolorao. Sendo assim, com esse tipo de corante, muitas vezes o profissional pode ter a impresso que j est na altura de tom desejada e enxaguar o cabelo antes de realmente chegar, o que acaba por atrapalhar o procedimento. Com o p descolorante branco, a visualizao do fundo de clareamento  mais fcil e o profissional pode realizar o procedimento com mais segurana." },
     
     { title: "Qual a proporo correta para misturar o p descolorante com OX?", description: "Nosso p descolorante foi desenvolvido para melhor atender s necessidades dos profissionais cabeleireiros e melhor se adaptar s tcnicas de clareamento. Sendo assim, o profissional poder fazer a mistura nas seguintes propores:  1 medida de p descolorante para 1 e meia medida de perxido (Ox);  1 medida de p descolorante para 2 medidas de perxido (Ox);  1 medida de p descolorante para 2 e meia medidas de perxido (Ox);  1 medida de p descolorante para 3 medidas de perxido (Ox). Essas medidas devero ser adequadas  tcnica pretendida e cabe lembrar que independente da proporo escolhida, a medida dever ser realizada com auxlio de balana de preciso, pois, alm de faz-las de forma correta o profissional ter noo da quantidade de produto utilizada para cada procedimento." },
     
     { title: "Qual  a diferena do perxido (oxigenada) da Sweet para as demais?", description: "A Ox da Sweet (perxido) no possui carga inica, portanto pode ser utilizada com toda e qualquer outra marca de colorao ou p descolorante, sem afetar a performance do produto e sem desestabilizar as misturas realizadas." },
     
     { title: "Para que serve o S.O.S Powder?", description: " um sistema de proteo em p para ser utilizado junto com o p descolorante ou colorao, que no compromete o desempenho do clareamento e descolorao dos cabelos, mas os protege dos efeitos nocivos do excesso de alcalinidade dos procedimentos qumicos." },
     
     { title: "Para que serve o passo 1 do S.O.S Impact Shock?", description: "O S.O.S 1 Impact Shock serve para vrias funes: neutralizar e parar a ao dos produtos alcalinos (descoloreo e relaxamentos) durante o procedimento, como forma de SOCORRO;  para proteger os cabelos antes de um processo qumico, aplicando-o diretamente na parte sensibilizada do cabelo (antes da qumica) ou misturando-o diretamente no produto (descolorao ou colorao);  para realizar um tratamento de reconstruo de alto impacto (ou cauterizao) junto com o S.O.S passo 2  Regeneration." },
     
     { title: "O passo 1 do S.O.S pode ser utilizado com a descolorao?", description: "Sim porque promove a reestruturao do fio do cabelo devolvendo resistncia e massa (reposio de protenas que normalmente so perdidas por conta dos procedimentos qumicos alcalinos. Possui pH de 2,5, cido ctrico, aminocidos, fludo de silicone e alta carga de queratina vegetal hidrolisada." },
     
     { title: "O passo 1 do S.O.S pode ser utilizado com outra mscara para finalizao da cauterizao ou de um tratamento profundo?", description: "Sim, pode. Indicamos para cabelos intensamente sensibilizados realizar o procedimento de cauterizao e finalizar com a Hair Cream Cronology para obteno de um sensorial mais macio, emoliente e com muito brilho." },
     
     { title: "Qual a indicao do kit Renovao Molecular Cronology?", description: "O kit Cronology  indicado para todos os tipos de cabelo e promove hidratao, nutrio e reconstruo de ma  neira inteligente. Ou seja, ao aplicar o produto, o mesmo ir realizar um mapeamento das necessidades dos fios e depositar somente o que for necessrio com o objetivo de manter a sade e a beleza dos mesmos." }
     
  ];

  shownGroup = null;

  constructor(public navCtrl: NavController) {
  }
  
  back(){
       this.navCtrl.setRoot(SuportePage);
  }
  toggleGroup(group) {
      if (this.isGroupShown(group)) {
          this.shownGroup = null;
      } else {
          this.shownGroup = group;
      }
  }
  isGroupShown(group) {
      return this.shownGroup === group;
  }
}


