import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { SuportePage } from '../../suporte/suporte';

@Component({
  selector: 'page-ti',
  templateUrl: 'ti.html'
})
export class TiPage {
      
diseases = [
     
    { title: "Esqueci a minha senha, como recuperar?", description: "Na pgina inicial do site www.ezitus.com, voc poder recuperar a sua senha, basta clicar em esqueci a minha senha e informar o seu login, voc receber um email para recuperar a senha." },
     
    { title: "Eu posso alterar os meus dados de Pessoa Fsica para Pessoa Jurdica?", description: "Sim, basta acessar o site www.suportesweet.com.br e abrir um chamado de alterao de cadastro na rea do Suporte TI." },
     
    { title: "O meu boto de saque no est liberado, o que fazer?", description: "Confirme se voc enviou corretamente os documentos para confirmao do seu cadastro." },
     
    { title: "Os meus documentos no foram aprovados, qual o motivo?", description: "Verifique se voc enviou documentos legveis, e se os dados do seu cadastro esto corretos." },
     
    { title: "Cadastrei uma pessoa e ela no apareceu na minha rede, o que fazer?", description: "Basta acessar o site www.suportesweet.com.br e abrir um chamado de localizao de usurio na rea do Suporte TI." },
     
    { title: "Estou tentando cadastrar um novo usurio e est dando erro, o que fazer?", description: "Basta acessar o site www.suportesweet.com.br e abrir um chamado de erro na rea do Suporte TI, informe o seu login e o CPF/CNPJ que est tentando cadastrar na mensagem." },
     
    { title: "Eu posso cadastrar mais de um usurio por CPF ou CNPJ?", description: "No, o sistema permite apenas um cadastro para cada documento." },
     
    { title: "Onde posso fazer o Download da apresentao e do contrato de Distribuidor Independente?", description: "Acesse seu escritorio virtual, clique em minha conta e posteriormente em Downloads." },
     
    { title: "Como fao para ativar um usurio com o meu saldo?", description: "No, uma vez cadastrado este usurio no poder mais trocar de posio." },
     
    { title: "Eu posso trocar de posio um distribuidor j cadastrado?", description: "Sim, desde que ainda no esteja ativo ou no tenha nenhum outro usurio abaixo dele." },
     
    { title: "Eu posso trocar de patrocinador? Posso trocar de equipe?", description: "No, uma vez cadastrado em uma equipe voc no poder mais mudar." }
     
  ];

    shownGroup = null;

  constructor(public navCtrl: NavController) {
  }

  back(){
       this.navCtrl.setRoot(SuportePage);
  }
  toggleGroup(group) {
      if (this.isGroupShown(group)) {
          this.shownGroup = null;
      } else {
          this.shownGroup = group;
      }
  }
  isGroupShown(group) {
      return this.shownGroup === group;
  }


}



