import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { SuportePage } from '../../suporte/suporte';

@Component({
  selector: 'page-logistica',
  templateUrl: 'logistica.html'
})
export class LogisticaPage {

  diseases = [
     
    { title: "Posso alterar meu endereo de entrega aps fazer o pedido?", description: "No. O endereo de envio  aquele que foi cadastrado no sistema. Confira se seu cadastro est correto para evitar atrasos." },
     
    { title: "Como posso acompanhar minha entrega?", description: "Atualmente, atravs do email do suporte. No futuro, o prprio sistema enviar o rastreio." },
     
    { title: "Como receberei meu pedido?", description: "Pelos correios. Fique atento para que sempre haja um responsvel para receber seu pedido no local de entrega." },
     
    { title: "Qual o valor do meu frete?", description: "O envio de Kits  por conta da Sweet. Para outras modalidades de compra, consulte o site da Vitrine." },
      
    { title: "Qual o prazo de recebimento?", description: "O prazo varia de acordo com a tabela dos correios." },
      
    { title: "Minha compra chegou danificada, como devo proceder?", description: "Faa fotos dos produtos danificados, mostrando claramente qual foi o problema e envie para o email do suporte sweet, juntamente com seu login, para fazermos a reposio dos produtos." },
      
    { title: "Minha compra chegou incompleta, como devo proceder?", description: "Faa fotos dos produtos danificados, mostrando claramente qual foi o problema e envie para o email do suporte sweet, juntamente com seu login, para fazermos a reposio dos produtos." },
      
     
    { title: "Minha compra chegou incorreta, como devo proceder?", description: "Envie um email para o suporte sweet, relatando seu caso. Ele ser averiguado e o time responder em 24h." }
     
  ];

  shownGroup = null;

  constructor(public navCtrl: NavController) {
  }

  back(){
       this.navCtrl.setRoot(SuportePage);
  }
  toggleGroup(group) {
      if (this.isGroupShown(group)) {
          this.shownGroup = null;
      } else {
          this.shownGroup = group;
      }
  }
  isGroupShown(group) {
      return this.shownGroup === group;
  }


}


